.pagination_container {
  display: flex;
  gap: 0.5rem;

  margin-top: 2rem;
  height: 2.5rem;
}

.pagination_btn {
  font-family: var(--font-verdana);

  user-select: none;
  font-weight: 500;
  font-size: 1rem;

  width: 2.5rem;
  border-radius: 0.3rem;

  box-shadow: 1px 4px 1px 1px rgba(124, 79, 224, 0.24) inset, 0.5px 0.5px 0.5px 0.5px #7d4fe029;
}

.pagination_btn[aria-checked='true'] {
  font-weight: 600;

  background-color: #7c4fe0;
  color: white;
}
