.footer_container {
  position: sticky;
  top: 0;

  height: 4rem;

  box-shadow: -1px 7px 5px -4px rgba(0, 0, 0, 0.35);
  background-color: #979fa8;
}

.footer {
  display: flex;
  align-items: center;
  justify-content: space-between;

  font-size: 1.3rem;

  margin: 0 auto;
  padding: 0.5rem 2rem;
  max-width: 1380px;
  height: 100%;

  color: whitesmoke;
}
