.main_container {
  display: flex;
  flex-grow: 1;

  margin-top: 2rem;
  margin-bottom: 4rem;
}

.main {
  display: flex;
  flex-direction: column;
  flex-grow: 1;

  max-width: 1380px;
  margin: 0 auto;
  padding: 0 2rem;
}

.main_table {
  font-family: var(--font-franklin);
  width: 100%;
}

.main_table > thead {
  position: sticky;
  top: 4rem;

  font-size: 1.3rem;
  text-transform: uppercase;

  height: 3rem;
  padding: 0.5rem;

  backdrop-filter: blur(4px);
}

.main_table > tbody > tr {
  font-family: var(--font-verdana);
  font-size: 1.2rem;
  height: 5rem;
}

.main_table > tbody > tr:nth-child(2n) {
  background-color: rgba(245, 222, 179, 0.254);
}
