.header_container {
  position: sticky;
  top: 0;

  height: 4rem;

  box-shadow: -1px 7px 5px -4px rgba(0, 0, 0, 0.35);
  background-color: #f6f6f6;
}

.header {
  display: flex;
  justify-content: space-between;

  margin: 0 auto;
  padding: 0.5rem 2rem;
  max-width: 1380px;
  height: 100%;
}

.header_info {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}

.header_time {
  font-family: var(--font-courier);
  font-weight: 600;
  font-size: large;
}
